/* src/index.css */

/* ============================================
   🌌 CYBERPUNK DESIGN SYSTEM
   ============================================ */

/* Tailwind CSS v3 */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* AlchanLayout global utilities */
.pb-safe { padding-bottom: env(safe-area-inset-bottom); }
@keyframes slide-up {
  from { transform: translateY(100%); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}
.animate-slide-up { animation: slide-up 0.3s ease-out; }
@keyframes spin-slow {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
.animate-spin-slow { animation: spin-slow 2s linear infinite; }

/* ============================================
   Reusable Cyberpunk Component Classes
   ============================================ */
@layer components {
  /* Section Card - gradient dark card */
  .cyber-section-card {
    @apply bg-gradient-to-br from-[#1e1e3f] to-[#16213e] rounded-[20px] p-6 mb-6 border-2 border-gray-700;
  }

  /* Section Title - purple accent */
  .cyber-section-title {
    @apply text-[#a78bfa] text-lg font-bold mb-4 flex items-center gap-2;
  }

  /* Modal Overlay */
  .cyber-modal-overlay {
    @apply fixed inset-0 bg-black/80 flex items-center justify-center z-[2000] p-5;
  }

  /* Modal Content */
  .cyber-modal-content {
    @apply bg-gradient-to-br from-[#1a1a2e] to-[#0f0f23] rounded-[20px] p-6 max-w-[400px] w-full border-2 border-gray-700;
  }

  /* Settings Button - list-style button */
  .cyber-settings-btn {
    @apply flex items-center gap-3 p-4 bg-[#0f0f23] rounded-xl border border-gray-700 cursor-pointer transition-all duration-200 hover:border-[#a78bfa]/50;
  }

  /* Modal Input */
  .cyber-modal-input {
    @apply w-full p-3.5 bg-[#0f0f23] border-2 border-gray-700 rounded-xl text-[#e8e8ff] text-sm mb-3 focus:border-[#a78bfa] focus:outline-none;
  }

  /* Primary Button (purple gradient) */
  .cyber-btn-primary {
    @apply flex-1 p-3 bg-gradient-to-br from-[#a78bfa] to-[#8b5cf6] border-none rounded-[10px] text-white text-sm font-semibold cursor-pointer disabled:opacity-70 disabled:cursor-not-allowed;
  }

  /* Cancel Button */
  .cyber-btn-cancel {
    @apply flex-1 p-3 bg-gray-700 border-none rounded-[10px] text-[#e8e8ff] text-sm cursor-pointer hover:bg-gray-600;
  }

  /* Info card display */
  .cyber-info-card {
    @apply p-4 bg-[#0f0f23] rounded-xl border border-gray-700;
  }
}

/* Google Fonts for Cyberpunk UI */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&family=Rajdhani:wght@300;400;500;600;700&family=Noto+Sans+KR:wght@300;400;500;600;700;800;900&display=swap');

/* ============================================
   Cyberpunk CSS Variables (Design Tokens)
   ============================================ */
:root {
  /* Dark Backgrounds */
  --cyber-bg-darkest: #050508;
  --cyber-bg-dark: #0a0a12;
  --cyber-bg-medium: #12121f;
  --cyber-bg-light: #1a1a2e;
  --cyber-bg-card: rgba(20, 20, 35, 0.85);
  --cyber-bg-glass: rgba(15, 15, 25, 0.75);

  /* Neon Accent Colors */
  --cyber-neon-cyan: #00fff2;
  --cyber-neon-cyan-dim: #00b8b0;
  --cyber-neon-magenta: #ff00ff;
  --cyber-neon-magenta-dim: #cc00cc;
  --cyber-neon-blue: #0088ff;
  --cyber-neon-purple: #8b5cf6;
  --cyber-neon-pink: #ff4ecf;
  --cyber-neon-yellow: #ffd166;
  --cyber-neon-green: #00ff88;
  --cyber-neon-orange: #ff6b35;
  --cyber-neon-red: #ff3366;

  /* Semantic Colors */
  --cyber-primary: var(--cyber-neon-cyan);
  --cyber-secondary: var(--cyber-neon-purple);
  --cyber-success: var(--cyber-neon-green);
  --cyber-warning: var(--cyber-neon-yellow);
  --cyber-danger: var(--cyber-neon-red);
  --cyber-info: var(--cyber-neon-blue);

  /* Text Colors */
  --cyber-text-primary: #e8e8ff;
  --cyber-text-secondary: #9999bb;
  --cyber-text-muted: #666688;
  --cyber-text-highlight: var(--cyber-neon-cyan);

  /* Borders */
  --cyber-border-dim: rgba(0, 255, 242, 0.15);
  --cyber-border-glow: rgba(0, 255, 242, 0.4);
  --cyber-border-bright: rgba(0, 255, 242, 0.7);

  /* Glow Effects */
  --cyber-glow-cyan: 0 0 15px rgba(0, 255, 242, 0.5), 0 0 30px rgba(0, 255, 242, 0.2);
  --cyber-glow-cyan-intense: 0 0 20px rgba(0, 255, 242, 0.8), 0 0 40px rgba(0, 255, 242, 0.4), 0 0 60px rgba(0, 255, 242, 0.2);
  --cyber-glow-magenta: 0 0 15px rgba(255, 0, 255, 0.5), 0 0 30px rgba(255, 0, 255, 0.2);
  --cyber-glow-purple: 0 0 15px rgba(139, 92, 246, 0.5), 0 0 30px rgba(139, 92, 246, 0.2);
  --cyber-glow-green: 0 0 15px rgba(0, 255, 136, 0.5), 0 0 30px rgba(0, 255, 136, 0.2);
  --cyber-glow-red: 0 0 15px rgba(255, 51, 102, 0.5), 0 0 30px rgba(255, 51, 102, 0.2);
  --cyber-glow-yellow: 0 0 15px rgba(255, 209, 102, 0.5), 0 0 30px rgba(255, 209, 102, 0.2);

  /* Card Shadows */
  --cyber-shadow-card: 0 4px 20px rgba(0, 0, 0, 0.5), 0 0 1px rgba(0, 255, 242, 0.3);
  --cyber-shadow-card-hover: 0 8px 30px rgba(0, 0, 0, 0.6), var(--cyber-glow-cyan);
  --cyber-shadow-inset: inset 0 1px 0 rgba(255, 255, 255, 0.05);

  /* Gradients */
  --cyber-gradient-header: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  --cyber-gradient-card: linear-gradient(180deg, rgba(25, 25, 45, 0.9) 0%, rgba(15, 15, 30, 0.95) 100%);
  --cyber-gradient-button: linear-gradient(135deg, rgba(0, 255, 242, 0.2) 0%, rgba(139, 92, 246, 0.2) 100%);
  --cyber-gradient-neon: linear-gradient(90deg, var(--cyber-neon-cyan), var(--cyber-neon-magenta));

  /* Border Radius */
  --cyber-radius-sm: 4px;
  --cyber-radius-md: 8px;
  --cyber-radius-lg: 12px;
  --cyber-radius-xl: 16px;

  /* Transitions */
  --cyber-transition-fast: 0.15s ease;
  --cyber-transition-normal: 0.25s ease;
  --cyber-transition-slow: 0.4s ease;
}

/* ============================================
   전역 폰트 크기 설정 - 가독성 최적화
   ============================================ */
html {
  font-size: 18px;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

/* 태블릿 (768px 이하) */
@media (max-width: 768px) {
  html {
    font-size: 17px;
  }
}

/* 모바일 (480px 이하) */
@media (max-width: 480px) {
  html {
    font-size: 16px;
  }
}

/* ============================================
   Cyberpunk Body Styles - Direct Colors
   ============================================ */
body {
  margin: 0;
  font-family: "Rajdhani", "Noto Sans KR", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "kern" 1, "liga" 1;
  background-color: #0a0a12 !important;
  background-image:
    linear-gradient(rgba(0, 255, 242, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 242, 0.03) 1px, transparent 1px) !important;
  background-size: 50px 50px !important;
  background-attachment: fixed !important;
  color: #e8e8ff !important;
  line-height: 1.6;
  letter-spacing: 0.02em;
  min-height: 100vh;
}

* {
  box-sizing: border-box;
}

code {
  font-family: "Orbitron", source-code-pro, Menlo, Monaco, Consolas, monospace;
  color: #00fff2;
  background: rgba(0, 255, 242, 0.1);
  padding: 0.1em 0.4em;
  border-radius: 4px;
}

/* ============================================
   전역 반응성 개선 유틸리티
   ============================================ */

/* 모바일 터치 영역 최소 크기 보장 */
button,
a,
input[type="button"],
input[type="submit"],
.clickable {
  min-height: 44px;
  min-width: 44px;
}

/* 모바일에서 작은 버튼 허용 */
@media (max-width: 768px) {

  .btn-sm,
  button.small,
  .small-button {
    min-height: 36px;
    min-width: 36px;
  }
}

/* 텍스트 가독성 개선 */
p,
li,
td,
th {
  line-height: 1.7;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.4;
}

/* 제목 폰트 스타일 */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  letter-spacing: -0.02em;
}

/* PC에서 가독성 좋은 폰트 크기 */
@media (min-width: 1024px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  h3 {
    font-size: 1.25rem;
  }

  h4 {
    font-size: 1.125rem;
  }

  p,
  li,
  td,
  th {
    font-size: 0.9375rem;
  }
}

/* 입력 필드 크기 개선 */
input[type="text"],
input[type="email"],
input[type="password"],
input[type="number"],
input[type="tel"],
textarea,
select {
  font-size: 1rem;
  padding: 0.75rem 1rem;
  border-radius: 8px;
}

@media (min-width: 1024px) {

  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="number"],
  input[type="tel"],
  textarea,
  select {
    font-size: 1.05rem;
    padding: 0.85rem 1.1rem;
  }
}

/* 카드 및 컨테이너 반응성 */
.page-container,
.card,
.panel,
.section-container {
  padding: 1rem;
}

@media (min-width: 768px) {

  .page-container,
  .card,
  .panel,
  .section-container {
    padding: 1.5rem;
  }
}

@media (min-width: 1024px) {

  .page-container,
  .card,
  .panel,
  .section-container {
    padding: 2rem;
  }
}

/* 그리드 반응성 개선 */
.responsive-grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr;
}

@media (min-width: 640px) {
  .responsive-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .responsive-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
  }
}

@media (min-width: 1280px) {
  .responsive-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

/* 모바일 스크롤 성능 개선 */
@media (max-width: 768px) {
  * {
    -webkit-overflow-scrolling: touch;
  }

  .scroll-container {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: none;
    -ms-overflow-style: none;
  }

  .scroll-container::-webkit-scrollbar {
    display: none;
  }
}

/* ============================================
   Cyberpunk UI Elements
   ============================================ */

/* Neon Scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--cyber-bg-medium);
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, var(--cyber-neon-cyan-dim), var(--cyber-neon-purple));
  border-radius: 4px;
  border: 1px solid var(--cyber-border-dim);
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, var(--cyber-neon-cyan), var(--cyber-neon-magenta));
  box-shadow: var(--cyber-glow-cyan);
}

/* Selection Style */
::selection {
  background-color: rgba(0, 255, 242, 0.3);
  color: var(--cyber-text-primary);
  text-shadow: 0 0 8px var(--cyber-neon-cyan);
}

/* Smooth transitions for interactive elements */
button,
a,
input,
select,
textarea {
  transition: all var(--cyber-transition-normal);
}

/* Cyberpunk Focus Style */
*:focus-visible {
  outline: 2px solid var(--cyber-neon-cyan);
  outline-offset: 2px;
  box-shadow: var(--cyber-glow-cyan);
}

/* Cyberpunk Card Hover Effects */
.hover-lift {
  transition: transform var(--cyber-transition-normal), box-shadow var(--cyber-transition-normal);
}

.hover-lift:hover {
  transform: translateY(-4px);
  box-shadow: var(--cyber-shadow-card-hover);
}

/* Cyberpunk Gradient Background */
.gradient-bg {
  background: var(--cyber-gradient-card);
}

/* Cyberpunk Font Classes */
.font-cyber {
  font-family: 'Orbitron', sans-serif;
  letter-spacing: 0.05em;
}

.font-cyber-ui {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
}

.font-display {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 700;
  letter-spacing: -0.02em;
}

/* 숫자 전용 폰트 (가독성) */
.tabular-nums {
  font-variant-numeric: tabular-nums;
}

/* ============================================
   Cyberpunk Animations
   ============================================ */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse-soft {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.7;
  }
}

/* Cyber Neon Glow Animation */
@keyframes neonPulse {

  0%,
  100% {
    box-shadow: 0 0 5px var(--cyber-neon-cyan), 0 0 15px rgba(0, 255, 242, 0.4);
  }

  50% {
    box-shadow: 0 0 20px var(--cyber-neon-cyan), 0 0 40px rgba(0, 255, 242, 0.6);
  }
}

@keyframes borderGlow {

  0%,
  100% {
    border-color: var(--cyber-border-dim);
  }

  50% {
    border-color: var(--cyber-border-bright);
  }
}

@keyframes scanLine {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(100vh);
  }
}

.animate-fadeIn {
  animation: fadeIn 0.3s ease-out;
}

.animate-slideUp {
  animation: slideUp 0.4s ease-out;
}

.animate-pulse-soft {
  animation: pulse-soft 2s ease-in-out infinite;
}

.animate-neon-pulse {
  animation: neonPulse 2s ease-in-out infinite;
}

.animate-border-glow {
  animation: borderGlow 2s ease-in-out infinite;
}

/* ============================================
   스크롤 문제 수정 - 전역
   ============================================ */

/* 메인 컨테이너 스크롤 수정 */
html,
body,
#root {
  min-height: 100%;
  overflow-x: hidden;
}

/* 페이지별 컨테이너 overflow 수정 */
.government-container,
.national-assembly-container,
.banking-container,
